    var wxCharts = require("../../../utils/wxcharts.js");
	var _self;
	var Charts;
	
	var Data = {
		series: [{
				data: 75,
				name: '优良'
			},
			{
				data: 12,
				name: '轻度污染'
			},
			{
				data: 2,
				name: '中度污染'
			},
			{
				data: 1,
				name: '重度污染'
			},
			{
				data: 0,
				name: '严重污染'
			}
		]
	};
	var width;
	export default {
		onLoad: function(event) {
			var detail = new Object();
			detail = JSON.parse(decodeURIComponent(event.detail))
			
			try {
				this.onload = JSON.parse(decodeURIComponent(event.detail));
			} catch (error) {
				this.onload = JSON.parse(event.detail);
			}
			console.log(this.onload);
			uni.setNavigationBarTitle({
				title: this.onload.date+this.onload.storeName+'空气统计'
			});	
			uni.getSystemInfo({
				success(res) {
					width = res.screenWidth - 10;
				}
			})
		},
		data() {
			return {
				onload:{}
			}
		},
		onReady: function() {
			this.ShowCharts("charts", Data);
			//this.hideLoading();
		},
		methods: {
			/*显示图表*/
			ShowCharts: function(canvasId, data) {
				Charts = new wxCharts({
					canvasId: canvasId,
					type: 'pie',
					fontSize: 11,
					background: '#FFFFFF',
					animation: true,
					series: data.series,
					width: width,
					height: 280,
					dataLabel: true,
					pixelRatio:1,
				});
			},
			goDetail:function(id,storeName){
				console.log(storeName);
				
				let detail = {
						id: id,
						storeName:storeName,
						date:this.onload.date
					}
					uni.navigateTo({
						url: "daytotal33?detail=" + encodeURIComponent(JSON.stringify(detail))
					})
			}
		}
	}
